.dashboard {
  width: 100%;
  height: 93vh;
  overflow: hidden;
  background-color: rgb(238, 238, 238);
}
.dashboardItem {
  box-sizing: border-box;
  border: 1px solid black;
  float: left;
  width: 50%;
  height: 50%;
}
@media (max-width: 600px) {
  .dashboardItem {
    width: 100%;
    height: 50%;
    float: none;
    border: none;
  }
  .dashboard {
    overflow: scroll;
  }
}
